<template>
    <div>
        <ProgressSpinner v-show="isLoadingData"/>
        <div class="box-table" v-show="isLoadingData == false">
            <!-- Dropdown list table -->
            <div class="list-table">
                <Dropdown v-model="selectedListTable" :options="listTable" optionLabel="name" placeholder="Select a Table" />
            </div>
            <!-- Show table by selected list table -->
            <h3>{{title}}</h3>
            <DataTable :value="products" responsiveLayout="scroll" columnResizeMode="fit"  class="table-report" scrollDirection="both" :paginator="true" :rows="20"
        paginatorTemplate="CurrentPageReport FirstPageLink PrevPageLink PageLinks NextPageLink LastPageLink RowsPerPageDropdown"
        :rowsPerPageOptions="[20,50,100]"
        currentPageReportTemplate="Showing {first} to {last} of {totalRecords}">
                <ColumnGroup type="header">
                    <Row>
                        <Column v-for="col of columns" :header="col.header" :key="col.field">
                        </Column>
                    </Row>  
                </ColumnGroup>
                <Column v-for="col of columns" :field="col.field" :key="col.field">
                    <template #body="{data}" v-if="col.field.toUpperCase().includes('YOUTUBE') || col.field.toUpperCase().includes('FANPAGE') || col.field.toUpperCase().includes('DESIGN') || col.field.toUpperCase().includes('DEV') || col.field.toUpperCase().includes('CONTENT')  || col.field.toUpperCase().includes('PR') || col.field.toUpperCase().includes('GROUP') || col.field.toUpperCase().includes('SOCIAL')">
                        <span v-if="data[col.field]">
                            {{data[col.field]['Count'] ? data[col.field]['Count'] : data[col.field]}}
                            <br/>
                            {{data[col.field]['Percent'] ? data[col.field]['Percent'] : data[col.field]}}
                        </span>
                        
                    </template>
                    <template #body="{data}" v-else>
                        {{data[col.field]}}
                    </template>
                </Column>
            </DataTable>
        </div>
    </div>
</template>
<script>
import newKPIJSON from './../../assets/NEW-KPI.json'
export default {
    name: 'ReportTable',
    props: ['title','searchValue'],
    data () {
        return {
            products: [],
            columns: [],
            data:newKPIJSON,
            listTable:[],
            listBrand:null,
            selectedListTable:'',
            searchValueCurrent: this.searchValue,
            isLoadingData: false
        }
    },
    created(){
        // this.getData()
        this.makeListTable()
    },
    watch: {
        searchValue(newValue, oldValue){
            if (JSON.stringify(newValue) !== JSON.stringify(oldValue)) {
                if (this.products != ''){
                    this.createTableAfterSelected(this.selectedListTable.name)
                    this.products = this.products.filter(val => {
                        return val.Brand == newValue.brand;
                        // console.log('checxxxk: ' + JSON.stringify(val.Brand))
                    })
                    // this.products.filter(val => val.brand === newValue.brand );
                }
            }
        },
        selectedListTable(newValue, oldValue){
            if (JSON.stringify(newValue) !== JSON.stringify(oldValue)) {
                this.createTableAfterSelected(newValue.code)
            }
        }
    },
    methods: {
        makeListTable(){
            if(this.data !=''){
                let ob = {}
                Object.keys(this.data).forEach(v => {
                    ob = {name: v, code: v}
                    this.listTable.push(ob)
                })
            }
        },
        createTableAfterSelected(nameTable){
            this.columns = []
            this.products = []
            
            this.products = this.data[nameTable]
            if (nameTable == 'TỔNG SỐ CÔNG VIỆC' || nameTable == 'HIỆU SUẤT LÀM VIỆC CỦA NHÂN VIÊN') {
                this.makeColumnByTableHasBrand()
            } else {
                this.makeColumnByTableNotHaveBrand()
            }
            
        },
        makeColumnByTableHasBrand(){
            let brand = 'VN'
            let obCL = {}
            let arrCheck = []
            let self = this
            // self.columns = []
            if (self.products.length > 0) {
                for (let index = 0; index < self.products.length; index++) {
                    if (self.products[index].Brand == brand){
                        Object.keys(self.products[index]).forEach(key => {
                            if (arrCheck.indexOf(key) === -1) {
                                arrCheck.push(key)
                                if(key == 'PicName') {
                                    obCL = {field: key, header: "Tên nhân viên - Team"}
                                } else if (key == 'MembersCount') {
                                    obCL = {field: key, header: "Số nhân viên"}
                                } else if (key == 'TotalJobs') {
                                    obCL = {field: key, header: "Tổng số công việc"}
                                } else if (key == 'CompleteRate') {
                                    obCL = {field: key, header: "Tỷ lệ hoàn thành"}
                                } else if (key == 'DeadlineLate') {
                                    obCL = {field: key, header: "Trễ Deadline"}
                                } else if (key == 'CompleteTime') {
                                    obCL = {field: key, header: "Thời gian thực hiện"}
                                } else if (key == 'WorkQuality') {
                                    obCL = {field: key, header: "Chất lượng công việc"}
                                } else if (key == 'KPI') {
                                    obCL = {field: key, header: "Điểm KPI"}
                                } else if (key == 'Status') {
                                    obCL = {field: key, header: "Trạng thái"}
                                } else if (key == 'TaskName') {
                                    obCL = {field: key, header: "Công việc"}
                                } else {
                                    obCL = {field: key, header: key}
                                }
                                self.columns.push(obCL)
                            }
                        })
                    } else {
                        continue
                    }
                }
            }
        },
        makeColumnByTableNotHaveBrand(){
            let obCL = {}
            let arrCheck = []
            let self = this
            // self.columns = []
            if (self.products.length > 0) {
                for (let index = 0; index < self.products.length; index++) {
                    Object.keys(self.products[index]).forEach(key => {
                        if (arrCheck.indexOf(key) === -1) {
                            arrCheck.push(key)
                            if(key == 'PicName') {
                                obCL = {field: key, header: "Tên nhân viên - Team"}
                            } else if (key == 'MembersCount') {
                                obCL = {field: key, header: "Số nhân viên"}
                            } else if (key == 'TotalJobs') {
                                obCL = {field: key, header: "Tổng số công việc"}
                            } else if (key == 'CompleteRate') {
                                obCL = {field: key, header: "Tỷ lệ hoàn thành"}
                            } else if (key == 'DeadlineLate') {
                                obCL = {field: key, header: "Trễ Deadline"}
                            } else if (key == 'CompleteTime') {
                                obCL = {field: key, header: "Thời gian thực hiện"}
                            } else if (key == 'WorkQuality') {
                                obCL = {field: key, header: "Chất lượng công việc"}
                            } else if (key == 'KPI') {
                                obCL = {field: key, header: "Điểm KPI"}
                            } else if (key == 'Status') {
                                    obCL = {field: key, header: "Trạng thái"}
                            } else if (key == 'TaskName') {
                                obCL = {field: key, header: "Công việc"}
                            } else {
                                obCL = {field: key, header: key}
                            }
                            self.columns.push(obCL)
                        }
                    })
                }
            }
        },
        getData(){
            // Begin call api
            this.data= ''
            this.isLoadingData = true
            let bodyFormData = new FormData();
                bodyFormData.append('loginToken', localStorage.getItem('jwt'));
                bodyFormData.append('mode', "requestKPI2");
                bodyFormData.append('startDate', this.searchValue.timeBegin);
                bodyFormData.append('endDate', this.searchValue.timeEnd);
            let self = this
            self.axios({
                method: "post",
                url: this.$urlAPI,
                data: bodyFormData,
                headers: { "Content-Type": "multipart/form-data" },
            })
            .then(response => {
                this.data = response.data

                // if (this.searchValue.brand == '') {
                //     this.data =''
                // } else {
                //     this.data = raw[0][this.searchValue.brand]
                //     if (this.data !== '') {
                //         this.makeColumnTable()
                //         this.makeDataTable()
                //     }
                // }
            }).catch(err => 
            {
                this.$toast.add({
                    severity:'error', 
                    summary: 'Error Message', 
                    detail:'Err' + err, life: 3000
                })
            }
            ).finally(() => this.isLoadingData = false)
        },
    }
}
</script>
<style>
.box-table h3{
    text-align: left;
}
.p-datatable{
    border: 1px solid #E7E8F2;
}
.table-report .p-datatable-wrapper table{
    width: 100% !important;
}
.list-table{
    text-align: left;
    float: left;
    width: 100%;
    margin-bottom: 20px;
}
</style>